import { Component, OnInit } from '@angular/core';
import { DatabaseService } from '../database.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-actor2movie',
  templateUrl: './actor2movie.component.html',
  styleUrls: ['./actor2movie.component.css']
})
export class Actor2movieComponent implements OnInit {
  moviesDB = [];
  actorId = '';
  movieId = '';
  actorsDB = [];

  constructor(private dbService: DatabaseService, private router: Router) { }

  onGetMovies() {
    this.dbService.getMovies().subscribe((data: any[]) => {
      this.moviesDB = data;
    });
  }

  onGetActors() {
    this.dbService.getActors().subscribe((data: any[]) => {
      this.actorsDB = data;
    });
  }

  ngOnInit() {
    this.onGetMovies();
    this.onGetActors();
  }

  onAddMovie() {
    const obj = {_id: this.movieId, actors: this.actorId};
    this.dbService.addMovie(obj).subscribe(result => {
      this.router.navigate(['/listmovies']);
    });
  }

}
